import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  tab: 213,
  scroll: {}
}

const mutations = {
  tab (state, val) {
  	state.tab = val
  },
  scroll (state, val) {
  	state.scroll = val
  }
}

export default new Vuex.Store({
	state,
	mutations
})


