import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import topic from '@/components/topic'
import user from '@/components/user'
import user_replies from '@/components/user_replies'

Vue.use(Router)

//配置路由
export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home/all'
    },
    {
    	path: '/home/:tab',
    	component: home
    },
    {
    	path: '/home/:tab',
    	component: home
    },
    {
    	path: '/home/:tab',
    	component: home
    },
    {
    	path: '/home/:tab',
    	component: home
    },
    {
    	path: '/home/:tab',
    	component: home
    },
    {
    	path: '/home/:tab',
    	component: home
    },
    {
    	path: '/topic/:id',
    	component: topic
    },
    {
    	path: '/user/:loginname',
    	component: user
    },
    {
    	path: '/user/user_replies/:loginname',
    	component: user_replies
    }
  ]
})



